
.search input {
		padding: 10px;
		border-radius: 4px;
		border: 1px solid #ddd;
		width: 300px;
}
.search input:focus, .search input:active {
    border: 1px solid #d9edf7;
}
.search button {
		padding: 8px;
    background: #fff;
    border: 0;
    border-radius: 4px;
    color: #666;
    font-size: 14px;
    border: 1px solid #ddd;
}




